import $ from 'jquery';
import React from 'react';
import { Link } from 'react-router-dom';

export class EditAddress extends React.Component {
  componentDidMount(): void {
    $('.window-background').click(() => {
      $('.window-container').removeClass('visible');
    });
    $('.window-content-close').click(() => {
      $('.window-container').removeClass('visible');
    });
  }
  render() {
    return (
      <React.Fragment>
        <div className="profile-address-edit_address-container window-container">
          <div className="profile-address-edit_address-background window-background">

          </div>
          <div className="profile-address-edit_address-content window-content">
            <div className="profile-address-edit_address-content-close window-content-close"></div>
            <div className="profile-address-edit_address-content-fields">
              <div className="profile-address-edit_address-content-fields-title">
                 
              </div>
              <div className="profile-address-edit_address-content-fields-input-container">
                <input className="profile-address-edit_address-content-fields-input" placeholder='. ,   336'></input>
                <input className="profile-address-edit_address-content-fields-input" placeholder=' / '></input>
                <input className="profile-address-edit_address-content-fields-input" placeholder=''></input>
                <input className="profile-address-edit_address-content-fields-input" placeholder=''></input>
                <input className="profile-address-edit_address-content-fields-input" placeholder=''></input>
              </div>
              <div className="profile-address-edit_address-content-fields-submit">
                
              </div>
            </div>
            <div className="profile-address-edit_address-content-map">
              <div className="profile-address-edit_address-content-map-title">
                  
              </div>
              <div className="profile-address-edit_address-content-map-overflow">
                
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    )
  }
}