    $('.catalog-product-title-action-tofavorites').click((e) => {
      if (!$(e.target).hasClass('catalog-product-title-action-tofavorites-active')) {
        $(e.target).addClass('catalog-product-title-action-tofavorites-active');
      } else if ($(e.target).hasClass('catalog-product-title-action-tofavorites-active')) {
        $(e.target).removeClass('catalog-product-title-action-tofavorites-active');
      };
    });
    $('.catalog-product-offers-analogue-tofavorites').click((e) => {
      if (!$(e.target).hasClass('catalog-product-offers-analogue-tofavorites-active')) {
        $(e.target).addClass('catalog-product-offers-analogue-tofavorites-active');
      } else if ($(e.target).hasClass('catalog-product-offers-analogue-tofavorites-active')) {
        $(e.target).removeClass('catalog-product-offers-analogue-tofavorites-active');
      };
    });
